import React, {useState} from "react";
import { quiz2 } from './day2/quiz2';
import Quiz from '../lib/Quiz'

function Day2() {

    const [quizResult, setQuizResult] = useState();

    const date = new Date().getDate();
    if(date<2){
        return(
            <h1 style={{ color: 'red' }}>Prosz nie kombinowa!</h1>
        );
    }
    return (
        <div>
            <h1 style={{ color: "white", marginLeft: "5rem" }}>
            Dzie 2 - Przygody.
            </h1>
            <div className="m-5 pb-15">
                <p style={{ fontStyle: 'italic' }}>If it scares you, it might be a good thing to try.</p>
                <p style={{ fontStyle: 'italic' }}>Seth Godin</p>
                <p className="mt-5" style={{ color: 'white' }}>Cze i czoem! Dzi porozmawiamy o rnych przygodach jakie miay miejsce w yciu najdzielniejszego karzeka w caej Danii i Polsce </p>
                <p style={{ color: 'white' }}>Wy, modzi bardzo lubicie uywa emotek. Dlatego przygody Twojego ycia zapisaem pismem obrazkowym. Sprbuj zgadn histori zanim popatrzysz na odpowiedzi - bdzie ciekawiej. Do boju i rozszyfruj je wszystkie!</p>
                {quizResult && quizResult.numberOfCorrectAnswers && quizResult.numberOfCorrectAnswers !== 10 && <h2 style={{ color: 'orange' }}> Sprbuj raz jeszcze</h2>}
                {quizResult && quizResult.numberOfCorrectAnswers && quizResult.numberOfCorrectAnswers === 10 && <h2 style={{ color: 'orange' }}> Klasunia! Twoja druga litera to "T". Podaj j dzi panu mowi, a czeka Ci niespodziewanka!</h2>}
                
            </div>
            <div className = "quizz" style={{background: '#007ea7'}}>
            <Quiz
                quiz={quiz2}
                shuffleAnswer
                showInstantFeedback
                continueTillCorrect
                onComplete={setQuizResult}
                onQuestionSubmit={(obj) => console.log('user question results:', obj)}
                disableSynopsis = {false}
                allowPauseTimer
                enableProgressBar
                
            />
            </div>

        </div>
    );
}
export default Day2;