import React, {useState} from "react";
import { quiz7 } from './day7/quiz7';
import Quiz from '../lib/Quiz'

function Day7() {
    const [quizResult, setQuizResult] = useState();

    const date = new Date().getDate();
    if(date<7){
        return(
            <h1 style={{ color: 'red' }}>Prosz nie kombinowa!</h1>
        );
    }
    return (
        <div>
            <h1 style={{ color: "white", marginLeft: "5rem" }}>
            Dzie 7 - Rozmowa.
            </h1>
            <div className="m-5 pb-15">
                <p style={{ fontStyle: 'italic' }}>Mwi mona z kadym  rozmawia bardzo mao z kim</p>
                <p style={{ fontStyle: 'italic' }}>Feliks Chwalibg</p>
                <p className="mt-5" style={{ color: 'white' }}>Rozmawiamy ze sob ju od wielu lat. Wymianiamy myli i zdjcia, chwile dobre, trudne, gupie. Czasem milczymy. Zmieniamy si i zmienia si te sposb w jaki za sob rozmawiamy.</p>
                <p style={{ color: 'white' }}>Przygotowaem dla Ciebie may quiz z naszego rozmawiania. Jeste w stanie zgadn, ktry to rok?</p>
                {quizResult && quizResult.numberOfCorrectAnswers && quizResult.numberOfCorrectAnswers !== 13 && <h2 style={{ color: 'orange' }}> Sprbuj raz jeszcze</h2>}
                {quizResult && quizResult.numberOfCorrectAnswers && quizResult.numberOfCorrectAnswers === 13 && <h2 style={{ color: 'orange' }}> Pikny wynik! Twoja sidma litera to "X"</h2>}
                
            </div>
            <div className = "quizz" style={{background: '#007ea7'}}>
            <Quiz
                quiz={quiz7}
                shuffle
                shuffleAnswer
                showInstantFeedback
                continueTillCorrect
                onComplete={setQuizResult}
                onQuestionSubmit={(obj) => console.log('user question results:', obj)}
                disableSynopsis = {false}
                allowPauseTimer
                enableProgressBar
                
            />
            </div>

        </div>
    );
}

export default Day7;