import React, { useState } from "react";
import { quiz5 } from './day5/quiz5.js';
import Quiz from '../lib/Quiz'

function Day5() {
    const [quizResult, setQuizResult] = useState();
    const date = new Date().getDate();
    if(date<5){
        return(
            <h1 style={{ color: 'red' }}>Prosz nie kombinowa!</h1>
        );
    }
    return (
        <div>
            <h1 style={{ color: "white", marginLeft: "5rem" }}>
            Dzie 5 - Czy wiesz za co Ci kocham?
            </h1>
            <div className="m-5 pb-15">
                <p style={{ fontStyle: 'italic' }}>Kocham Ci!</p>
                <p style={{ fontStyle: 'italic' }}>Jakub Brzosko</p>
                <p className="mt-5" style={{ color: 'white' }}>Cze. Wiem e musiaa czeka dug chwil eby to usysze. Dlatego dzi chciaem cho w maym stopniu nadrobi za te wszystkie lata.</p>
                {quizResult && quizResult.numberOfCorrectAnswers && quizResult.numberOfCorrectAnswers !== 10 && <h2 style={{ color: 'orange' }}> Sprbuj raz jeszcze</h2>}
                {quizResult && quizResult.numberOfCorrectAnswers && quizResult.numberOfCorrectAnswers === 10 && <h2 style={{ color: 'orange' }}> Za wszystkie przysze powody dla ktrych bd Cie kocha dostajesz ode mnie literk "S"</h2>}
                
            </div>
            <div className = "quizz" style={{background: '#007ea7'}}>
            <Quiz
                quiz={quiz5}
                shuffle
                shuffleAnswer
                showInstantFeedback
                continueTillCorrect
                onComplete={setQuizResult}
                onQuestionSubmit={(obj) => console.log('user question results:', obj)}
                disableSynopsis = {false}
                allowPauseTimer
                enableProgressBar
                
            />
            </div>

        </div>
    )
}

export default Day5;