import React from "react";

function Day9() {
    const date = new Date().getDate();
    if(date<9){
        return(
            <h1 style={{ color: 'red' }}>Prosz nie kombinowa!</h1>
        );
    }

    return (
        <div>
            <h1 style={{ color: "white", marginLeft: "5rem" }}>
                <p>Dzie 9</p>
            </h1>
            <div className="m-5 pb-15">
                <p >Cze Misiaku! Witem Ci w tym piknym miesicu, kiedy skoczysz 30 latek. O rany ale to zleciao!</p>
                <p >Z tej okazji chiabym zabra Ci w podr. Kadego dnia jedna kartka z kalendarza bdzie si aktywowa. Kada z nich to jakie unikalne wspomnienie, wyzwanie, prezent albo gra. Jeeli uda Ci si ukoczy wszystkie z nich, czeka Ci -tytyrytyty- Nagroda Gwna!</p>
                <p > Mam nadziej e te podr sprawi Ci rado, tak jak inne nasze podre. Wszystkiego najlepszego po raz pierwszy. Kocham Ci! </p>      
                <div className="">
            <h4 className="mt-5" style={{color: 'white'}}> Kada podr zaczyna si od pierwszego kroku. My wykonalimy razem wiele pierwszych z nich. Czy pamitasz je wszystkie?</h4>

            </div>          
            </div>


        </div>
    );
}

export default Day9;