import React from "react";
import auto from './day1/auto.jpg';
import auto2 from './day1/2auto.jpg';
import azja from './day1/azja.jpg';
import czolg from './day1/czolg.jpg';
import dziara from './day1/dziara.jpg';
import lyzwy from './day1/lyzwy.jpg';
import magisterka from './day1/magisterka.jpg';
import mieszko from './day1/mieszko.jpg';
import remont from './day1/remont.jpg';
import rollercoaster from './day1/rollercoaster.jpg';
import rower from './day1/rower.jpg';
import sushi from './day1/sushi.jpg';
import sushirunning from './day1/sushirunning.jpg';
import wakacje from './day1/wakacje.jpg';
import weekend from './day1/weekend.jpg';
import wyboj from './day1/wyboj.jpg';


function Day1() {

    const date = new Date().getDate();
    if(date<1){
        return(
            <h1 style={{ color: 'red' }}>Prosz nie kombinowa!</h1>
        );
    }

    return (
        <div>
            <h1 style={{ color: "white", marginLeft: "5rem" }}>
                <p>Dzie 1 - pierwszy krok</p>
            </h1>
            <div className="m-5 pb-30">
                <div className="m-50">
                    <h4 className="mt-10 pb-30" style={{ color: 'white' }}> Kada podr zaczyna si od pierwszego kroku. My wykonalimy razem wiele pierwszych krokw. Pamitasz je wszystkie?</h4>
                    <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }} >
                    <img style={{width:'70%'}} src={auto} className="mt-5"/> 
                    <p>Pamitasz abcie, nasze pierwsze autko? Robio nam czasem psikusy, ale byo super. </p>
                    <img style={{width:'70%'}} src={auto2} className="mt-5"/> 
                    <p>Bkitka, nasze pierwsze (i obecne) dorose auto? Ciekawe ile jeszcze aut przed nami.</p>
                    <img style={{width:'70%'}} src={azja} className="mt-5"/> 
                    <p>Pierwsz wspln wycieczk na inny kontynent? Pyszne jedzonko, gorco, sonie i te emocje. Kurcze to byo co!</p>
                    <img style={{width:'70%'}} src={czolg} className="mt-5"/> 
                    <p>Pierwsz wspln wizyt w czogu?</p>
                    <img style={{width:'70%'}} src={dziara} className="mt-5"/> 
                    <p>Pierwsz dziark?</p>
                    <img style={{width:'70%'}} src={lyzwy} className="mt-5"/> 
                    <p>Jak pierwszy raz poszlimy razem na ywy na Fredziu?</p>
                    <img style={{width:'70%'}} src={magisterka} className="mt-5"/> 
                    <p>No to na pewno pamitasz, Twoja pierwsza praca magisterska! Piknie obroniona w Covidzie.</p>
                    <img style={{width:'70%'}} src={mieszko} className="mt-5"/> 
                    <p>Nasz pierwszy wsplny dom. Prawie 5 lat temu. Jejku, ile tu si wydarzyo. Ksik mona by napisa.</p>
                    <img style={{width:'70%'}} src={remont} className="mt-5"/> 
                    <p>No i oczywicie pierwszy szalony remont. Do dzi si czasem zastanawiam jak mymy to ogarnli. Grubo.</p>
                    <img style={{width:'70%'}} src={rollercoaster} className="mt-5"/> 
                    <p>Pierwszy wsplny rollercoaster. Modzi pikni zakochani.</p>
                    <img style={{width:'70%'}} src={rower} className="mt-5"/> 
                    <p>A to co za projekt? Pierwszy wsplny rower.</p>
                    <img style={{width:'70%'}} src={wyboj} className="mt-5"/> 
                    <p>No i pierwszy wybj na naszej wsplnej drodze. Pamitam dobrze jak do mnie wtedy zadzwonia i jaki byem przestraszony.</p>      
                    <img style={{width:'70%'}} src={sushi} className="mt-5"/> 
                    <p>Dawno temu, ale to pierwsze wsplnie zrobione sushi - jeszcze na Lea!</p>
                    <img style={{width:'70%'}} src={sushirunning} className="mt-5"/> 
                    <p>A to z kolei pierwsze sushi bez umiaru. 35 kawakw na osob, szalestwo. Rekord nie pobity do dzi.</p>
                    <img style={{width:'70%'}} src={wakacje} className="mt-5"/> 
                    <p>Pierwsze prawdziwe wsplne wakajki. Chorwacja . Ju jako oficjalna para.</p>
                    <img style={{width:'70%'}} src={weekend} className="mt-5"/> 
                    <p className="m-25">A tu z kolei najpierwsiejszy wsplny weekend trip - Oslo - z ktrego prawie udao si nam nie wrci.</p>
                    </div>
                    <h5 className="mt-5" style={{ color: 'white' }}> Dugo mona by tak wymienia. Chciaem Ci powiedzie, e straszni si ciesz e mogem przej z Tob tyle pierwszych krokw. Wiem, e czeka nas jeszcze mnstwo kolejnych. Mam nadziej e ju do koca bdziemy kroczy razem przez ycie, a kady nasz krok bdzie na swj sposb pierwszy. Twoja pierwsza nagroda, to literka "G". Zapisz j sobie i nie zgub, przyda Ci si jeszcze. Buziaki :* </h5>
                    <h5 style={{ color: 'grey' }}>Twj M </h5>
                </div>
            </div>
        </div>
    );
}



export default Day1;

// pierwszy pocaunek
